<template>
  <div>
    <van-nav-bar
      title="批量转账"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <form action="/">
      <van-search placeholder="请输入币种名称" v-model="searchValue" 
        @search="onSearch"
        @cancel="onCancel" />
    </form>
    <van-notice-bar :scrollable="false" class="mt-text-center">
      什么是批量转账？
    </van-notice-bar>
    <!-- <van-button @click="handleAddData">add data</van-button>
    <van-button @click="handleGetData">get data</van-button> -->
    <van-list v-if="!searchEmpty">
      <div
        v-for="(Contract,index) in filterContracts"
        :key="index"
        @click="handleContractClick(Contract)"
        class="contract van-hairline--top-bottom mt-fs-14 mt-flex mt-align-center mt-justify-between">
        <div>
          <mt-proxy-img :src="Contract.logo" :width="36" :height="36"/>
        </div>
        <div class="contract-content">
          <div class="contract-content-left">
            <div>
              {{Contract.symbol}}
              <span v-if="Contract.contract === '0x0'">
                暂不支持ETH转账
              </span>
            </div>
            <div>{{Contract.name}}</div>
          </div>
          <div
            v-if="Contract.contract !== '0x0'" class="contract-arrow">
            <van-icon name="arrow"></van-icon>
          </div>
          <!-- <nuxt-link
            :to="'/dapp/transfer/detail?'+ stringify(Contract)"
            v-if="Contract.contract !== '0x0'" class="contract-arrow">
            <van-icon name="arrow"></van-icon>
          </nuxt-link> -->
        </div>
      </div> 
    </van-list>
    <div class="filter-nothing" v-if="!filterContracts.length">
      <div>
      </div>
      <p>
        暂无相关搜索结果
      </p>
    </div>
  </div>
</template>

<script>
import MtProxyImg from "@/components/MtProxyImg";
import qs from "qs";
import { SetSessionObj } from "@/utils/utils";
export default {
  name: "transfer-list",
  data() {
    return {
      // utils
      stringify: qs.stringify,
      listData: [],
      searchValue: "",
      // CurrentWalletContracts: [],
      filterContracts: [],
      CurrentWalletContracts:  [{
        "contract":"0x0",
        "created_at":"1525706049",
        "decimals":18,
        "is_infrastructure":"1",
        "logo":"http://p0h01hbw2.bkt.clouddn.com/3c1ac1a7537528c71e80bceb4741d578",
        "name":"Ethereum",
        "suggest_gas":60000,
        "symbol":"ETH",
        "type":1},
        {
          "contract":"0x0f5d2fb29fb7d3cfee444a200298f468908cc942",
          "created_at":"1530071256",
          "decimals":18,
          "is_infrastructure":"0",
          "logo":"http://p0h01hbw2.bkt.clouddn.com/decentraland.png",
          "name":"Decentraland",
          "suggest_gas":60000,
          "symbol":"MANA",
          "type":2},
          {"contract":"0x0abdace70d3790235af448c88547603b945604ea","created_at":"1530071256","decimals":18,"is_infrastructure":"0","logo":"http://p0h01hbw2.bkt.clouddn.com/373893383cbd08606840881c9c0015fb","name":"district0x","suggest_gas":60000,"symbol":"DNT","type":2},{"contract":"0xd850942ef8811f2a866692a623011bde52a462c1","created_at":"1530071256","decimals":18,"is_infrastructure":"0","logo":"http://p0h01hbw2.bkt.clouddn.com/e6fb01a5f202b110e97e43f5aae078ee","name":"VeChain","suggest_gas":60000,"symbol":"VEN","type":2}],
      // 状态
      searchEmpty: false
    };
  },
  components: {
    MtProxyImg
  },
  watch: {
    CurrentWalletContracts(value) {
      this.filterContracts = value
    }
  },
  mounted() {
    this.filterContracts = this.CurrentWalletContracts
    this.initPossiableFunc();
    setTimeout(() => {
      window.Ygritte &&
        window.Ygritte.getCurrentWalletContracts("MTgetCurrentWalletContracts");
    }, 0);
  },
  methods: {
    onClickLeft() {
      console.log("top-left-click");
      window.Ygritte && window.Ygritte.closeWebView();
    },
    onSearch() {
      const newContracts = Object.assign([], this.CurrentWalletContracts)
      this.filterContracts = newContracts.filter((item)=> {
        return item.symbol.indexOf(this.searchValue)>-1 || item.name.indexOf(this.searchValue)>-1
      })
    },
    onCancel() {
      
    },
    handleContractClick(contract) {
      if (contract.contract === "0x0") {
        return;
      }
      SetSessionObj("contract", contract);
      this.$router.push("/dapp/transfer/detail");
    },
    initPossiableFunc() {
      let that = this;
      window.MTgetCurrentWalletContracts = function(res) {
        console.log(res);
        that.CurrentWalletContracts = res;
      };
      window.getData = function(res){
        console.log(res)
      }
      window.addData = function(res){
        console.log(res)
      }
    },
    handleAddData() {
      window.Ygritte && window.Ygritte.syncData(1, 'data', 'data', 'addData');
    },
    handleGetData() {
      window.Ygritte && window.Ygritte.syncData(0, 'data', '', 'getData');
    }
  }
};
</script>

